$('#file').click(function(){
    setTimeout(function(){
        // var userName = $('.user-name').find('input').val();
        // var userTit = $('.user-tit').find('input').val();
        // var userTxar = $('.user-txar').find('textarea').val().replace(/^ +| +$/g,'');
        // var userTxar = $('.user-txar').find('.txar').html();
        // userTxar2=userTxar.replace(/\n|\r\n/gm,"<br />");
        $('.J-hide').hide();
        $('.infobg').css('margin',"0 .31rem");
        $('.text-top').hide();
        $('.clip-bot').show();
        // $('.iptbox > *').hide();
        // $('.iptbox > .txt-var').show();
        $('.btn-up').hide();
        // $('.name-val').show().text(userName);
        // $('.tit-val').show().text(userTit);
        // $('.txar-val').show().html(userTxar);

        $('.iptbox').addClass('iptbox_no');
        $('.qr-code').show();

        var userTit = $('.user-tit').find('.ipt').val();
        if(!userTit){
            $('.user-tit').find('.ipt').hide();
        }

        var userTxar = $('.user-txar').find('.txar').val();
        if(!userTxar){
            $('.user-txar').find('.txar').hide();
        }
    },3000)
});
