$('.J-c1').click(function(){
	$(this).addClass('hide');
	$('.J-c2').removeClass('hide');
	$('.iptbox').removeClass('ipt-c1').addClass('ipt-c2');
})
$('.J-c2').click(function(){
	$(this).addClass('hide');
	$('.J-c3').removeClass('hide');
    $('.iptbox').removeClass('ipt-c2').addClass('ipt-c3');
})
$('.J-c3').click(function(){
	$(this).addClass('hide');
	$('.J-c1').removeClass('hide');
	$('.iptbox').removeClass('ipt-c3').addClass('ipt-c1');
})
