$('.J-secbtn').click(function(){
    $('.layer-wrap').show();
});
$('.J-cloes').click(function(){
    $('.layer-wrap').hide();
});
$('.J-radio').click(function(){
    $('.r-icn').removeClass('active');
    $(this).find('.r-icn').addClass('active');
    // $('.user-txar').find('.txar').html($(this).find('.flex1').html());
    // $('.user-txar').find('.txar').text($(this).find('.flex1').html());
    var tempHtml = '';
    var $ps = $(this).find('.flex1 p');
    for(var i=0;i<$ps.length;i++){
        tempHtml += $ps[i].innerText + '\n';
    }
    $('.user-txar').find('.txar').text(tempHtml);
});
